// Importing necessary modules and components
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Audio } from 'react-loader-spinner';
import { Switch, FormControlLabel, Button } from '@mui/material';
import axios from 'axios';

// Importing actions and selectors from Redux slices
import {
  fetchPage,
  fetchFirstPage,
  selectIsLoading,
  selectAllDataWhosePagesHaveBeenLoaded,
  saveCurrentPageInAllDataWhosePagesHaveBeenLoaded,
  selectCurrentPage,
  selectIsPagination,
  pagePagination,
  ChangeCurrentNumberAftenLoadedPageWithHelpScroll
} from './redux/slices/pageSlice';
import {
  modalStatusChange,
  currentPersonageForWatch,
  changeCurrentWidthWindow
} from './redux/slices/modalSlice';
import Personage from './components/personage';
import KeepMountedModal from './components/KeepMountedModal';
import PaginationControlled from './components/Pagination';
import styles from './App.module.scss';
import { setError, selectError } from './redux/slices/errorSlice';
import ErrorPage from './pages/ErrorPage';
import getCurrentPageNumber from './utilites/getCurrentPageNumber';
import Error from './components/Error';
import { scrollToTop } from './utilites/scrollToTop';


// Fetching data from the API
const api = await axios.get("https://rickandmortyapi.com/api/character/")
  .then((res) => res.data)
  .catch((err) => err)

function App() {
  // Redux state and dispatch setup
  const allDataPage = useSelector(selectAllDataWhosePagesHaveBeenLoaded)
  const currentP = useSelector(selectCurrentPage)
  const paginationStatus = useSelector(selectIsPagination)
  const isLoading = useSelector(selectIsLoading)
  const dispatch = useDispatch()

  // Handling screen width changes
  window.onresize = () => dispatch(changeCurrentWidthWindow(document.body.offsetWidth))


  // Function to load the first page when the user starts the app
  const getFirstPage = () => {
    if (api.name !== "AxiosError") {
      dispatch(fetchFirstPage(api))
    } else {
      dispatch(setError())
    }
  }
  // Checking if it's the first launch and whether the first page needs to be loaded
  const loadFirstPage = useSelector((state) => (!!state.page.currentDataFromServer.info))
  if (!loadFirstPage) {
    getFirstPage()
  }


  // Determining the URL for the next page based on the current API response and pagination status
  const nextPageApi = useSelector((state) => {
    if (api.name !== "AxiosError") {
      // If the current API response indicates an error, set nextPageApi to null
      return state.page.currentDataFromServer.info.next === null
        ? `https://rickandmortyapi.com/api/character/?page=${state.page.currentPageNumber}`
        : state.page.currentDataFromServer.info.next;
    } else {
      return null;
    }
  });

  // Getting the current list of personages
  const listWithPers = useSelector((state) => state.page.currentDataFromServer.results)

  // Checking the status for the MODAL WINDOW, opened or closed.
  const modalWindowStatus = useSelector((state) => state.modal.modalWindow)

  // When the modal window is opened, getting current data for the current personage
  const currentPersonageForModal = useSelector((state) => state.modal.currentPersonage)



  // Finding if the next page in data is saved in the client
  const findNextPageInAllDataPage = (allDataPage.filter((page) => page.info.next !== nextPageApi));


  // Function to open the modal window with the current personage
  const openModalWindow = (event) => {
    event = event || window.event;
    event.preventDefault();

    // Extract the current event ID from the target or its parent element
    const currentEventId = event.target.id ? event.target.id : event.target.parentElement.id;

    // Find the matching personage based on the event ID, considering pagination status
    let page = '';
    !paginationStatus
      ? allDataPage.flatMap(pages => pages.results.find(p => +p.id === +currentEventId && (page = p)))
      : currentP.results.find(p => +p.id === +currentEventId && (page = p));
    // Dispatch actions to update the modal state
    dispatch(currentPersonageForWatch(page));
    dispatch(modalStatusChange());
  };

  // Handling scroll events for loading more data
  window.onscroll = () => (!paginationStatus) ? loadingNextPageIfScroll() : null


  // Function for loading more data when scrolling
  function loadingNextPageIfScroll() {
    if (api.name !== "AxiosError") {
      const height = document.body.offsetHeight
      const screenHeight = window.innerHeight
      const threshold = height - screenHeight
      const scrollPosition = window.scrollY || document.documentElement.scrollTop;
      const isBelowLowerThreshold = threshold / 1.20 < scrollPosition;
      const isAboveUpperThreshold = threshold / 1.30 > scrollPosition;
      if (isBelowLowerThreshold || isAboveUpperThreshold) {
        // don't use
      } else if (!paginationStatus) {
        const currentPageNumber = getCurrentPageNumber(currentP);
        dispatch(ChangeCurrentNumberAftenLoadedPageWithHelpScroll(currentPageNumber));
        fetchNextPage();
      }
    }
  }

  // Function to fetch the next page
  const fetchNextPage = () => {
    if (!findNextPageInAllDataPage.length === 0) {
      dispatch(fetchPage(nextPageApi))
      dispatch(saveCurrentPageInAllDataWhosePagesHaveBeenLoaded(currentP))
    } else {
      dispatch(fetchPage(nextPageApi))
    }
  }

  // Handling errors
  const selectErrorStatus = useSelector(selectError)
  if (api.name === "AxiosError") {
    return <ErrorPage {...api} />
  } else {
    return (
      <div className={styles.App}>
        {selectErrorStatus ? <Error {...api} /> : ''}
        <div className={styles.btnsTopAndPanding} >
          <div className={styles.btnToTop}> <Button buttontotop="btnToTop"
            variant="contained" color="success" onClick={() => {
              scrollToTop()
            }}>
            to top
          </Button></div>
          <div className={styles.pagination} onChange={() => { dispatch(pagePagination()) }}><FormControlLabel
            value="start"
            control={<Switch color="primary" />}
            onChange={(event) => (event.target.checked) ? scrollToTop() : null}
            label="pagination"
            labelPlacement="start"
          /></div>
          <div className={styles.paginationControlled}>
            {paginationStatus ? <PaginationControlled {...currentP} /> : null}
          </div>
        </div>

        <div className={styles.contaner}>
          {paginationStatus ? listWithPers.map((pers) => <Personage key={pers.id} id={pers.id} onClick={openModalWindow} namePersonage={pers.name} img={pers.image} />) :
            allDataPage.map((d) => d.results.map((pers) => <Personage key={pers.id} id={pers.id} onClick={openModalWindow} namePersonage={pers.name} img={pers.image} />))
          }
          {isLoading ? <Audio className={styles.AudioLoading}
            height="200"
            width="200"
            c
            color="#4fa94d"
            ariaLabel="audio-loading"
            wrapperStyle={
              {
                zIndex: "100",
                position: "fixed",
                top: "50%",
                left: "50%",
                marginRight: "-50%",
                transform: " translate(-50%, -50%) "
              }
            }
            wrapperClass="wrapper-class"
            visible={true}

          /> : ''}
          {modalWindowStatus ? <KeepMountedModal {...currentPersonageForModal} /> : ''}
        </div>
      </div>
    )
  }
}

export default App;
